                datetime(offset) {
                    var d = new Date().getTime() - offset;

                    var timeZ = new Date(d);
                    var a = timeZ.getHours();
                    var b = timeZ.getMinutes();
                    var c = timeZ.getSeconds();
                    
                    var now = new Date();
                    var hrs = now.getHours();
                    var mins = now.getMinutes();
                    var secs = now.getSeconds();
                    var todayOffset = (hrs*3600 + mins*60 + secs)*1000;

                    var time = "";
                    
                    if(d <= 3600000 && d <= todayOffset){
                        time = `${b} minute(s) ago`;
                    }else if(3600000 < d && d <= todayOffset){
                        time = `${a} hour(s) ago`;
                    }else if(todayOffset < d && d  <= todayOffset + 86400000){
                        time = 'yesterday';
                    }else{
                        var p = new Date(offset);
                        var date = p.getDate() < 10? "0" + p.getDate() : p.getDate();
                        var month = p.getMonth() + 1 <10?"0" + (p.getMonth() + 1) : p.getMonth() + 1;
                        var year = p.getFullYear();
                        time = [date, month, year].join('/');
                    }
                    return time;
                },

